body {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

.search-form {
  background-color: #0ff;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.photo-card {
  color: #302c5a;
  border-radius: 4px;
  font-size: 14px;
  box-shadow: 0 0 12px #302c5a;
}

.photo-card:hover {
  transform: scale(1.1);
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 5px;
  display: flex;
}

.info {
  justify-content: space-between;
  padding: 0 15px;
  display: flex;
}

.info-item {
  text-align: center;
}

img {
  width: 380px;
  height: 240px;
  object-fit: cover;
  transition: all .5s;
  display: block;
}

button {
  cursor: pointer;
  background-color: #5385e3;
}

.load-more {
  margin: 30px auto 0;
  display: block;
}

.load-more:disabled {
  cursor: none;
  background-color: #c1c1c1;
}

